body {
  background: #F6F7FB;
}
.resources-collection {
  width: 100%;
  display: block;
  min-width: 1366px;
}
.resources-collection .clearfix {
  margin: 0 10px 15px 10px;
}
.resources-collection .clearfix:after {
  content: '';
  display: block;
  height: 0;
  overflow: hidden;
  clear: both;
  zoom: 1;
}
.resources-collection .collection-pic {
  display: block;
  position: relative;
  margin: 0 26px;
}
.resources-collection .collection-pic .ant-tabs-bar {
  padding-left: 160px;
}
.resources-collection .collection-pic .ant-tabs-bar .ant-tabs-tab {
  padding: 0;
  height: auto;
  line-height: 30px;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  color: #2A283D;
  opacity: 0.4;
}
.resources-collection .collection-pic .ant-tabs-bar .ant-tabs-tab-active {
  opacity: 1;
}
.resources-collection .collection-pic .top {
  display: inline-block;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
.resources-collection .collection-pic .top .span {
  font-size: 28px;
  line-height: 120.7%;
  color: #2A283D;
}
.resources-collection .collection-pic .top .sel {
  width: 100px;
  height: 30px;
  background: #F5F5F5;
  border: 1px solid #E6E6E6;
  box-sizing: border-box;
  border-radius: 3px;
  margin: 0 10px;
}
.resources-collection .collection-pic .top .sel .ant-select-selection {
  height: 30px;
  line-height: 28px;
  border: none;
  background: none;
}
.resources-collection .collection-pic .top .sel .ant-select-selection .ant-select-selection__rendered {
  line-height: 28px;
}
.resources-collection .collection-pic .top .sel .ant-select-selection .ant-select-arrow {
  font-size: 14px;
  color: #CACACA;
  border-radius: 1px;
  transform: rotate(180deg);
  margin-top: -10px;
}
.resources-collection .collection-pic .top .ant-radio-group {
  display: inline-block;
  width: auto;
  margin: 0 30px;
  position: relative;
  top: -5px;
}
.resources-collection .collection-pic .top .ant-radio-group .ant-radio-button-wrapper {
  padding: 0 5px;
  border: none;
  border-bottom: none;
  background: none;
  border-radius: 0;
  margin: 0 10px;
}
.resources-collection .collection-pic .top .ant-radio-group .ant-radio-button-wrapper .ant-radio-button {
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 120.7%;
  color: #2A283D;
  opacity: 0.4;
}
.resources-collection .collection-pic .top .ant-radio-group .ant-radio-button-wrapper .ant-radio-button span {
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 120.7%;
  color: #2A283D;
  opacity: 0.4;
}
.resources-collection .collection-pic .top .ant-radio-group .ant-radio-button-wrapper:not(:first-child)::before {
  display: none;
}
.resources-collection .collection-pic .top .ant-radio-group .ant-radio-button-wrapper.ant-radio-button-wrapper-checked {
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 30px;
  color: #2A283D;
  opacity: 1;
  border-bottom: 2px solid #3CB9FF;
  -webkit-box-shadow: none;
  box-shadow: none;
}
.resources-collection .collection-pic .content {
  background: #FFFFFF;
  border-radius: 4.5705px;
  width: 100%;
  margin: 20px auto 15px;
  display: block;
  padding: 35px 35px 0;
}
.resources-collection .collection-pic .bottom {
  display: block;
  background: #FFFFFF;
  border-radius: 4.5705px;
  padding: 30px 20px;
  width: 100%;
}
.resources-collection .collection-pic .bottom .bottom-title {
  width: 100%;
  display: block;
  padding-bottom: 15px;
  border-bottom: 1px solid #EEEEEE;
}
.resources-collection .collection-pic .bottom .bottom-title .bottom-left {
  float: left;
  display: inline-block;
}
.resources-collection .collection-pic .bottom .bottom-title .bottom-left .ant-btn {
  background: #3BB9FF !important;
  border-radius: 2px;
  border: 1px solid #3BB9FF !important;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 19px;
  color: #fff;
  padding: 0 18px 0 15px;
  position: relative;
  margin-right: 10px;
  min-width: 90px;
}
.resources-collection .collection-pic .bottom .bottom-title .bottom-left .ant-btn .i {
  position: absolute;
  right: 5px;
  top: 11px;
  width: 0;
  height: 0;
  border-right: 4px solid transparent;
  border-left: 4px solid transparent;
  border-bottom: 4px solid #fff;
  content: "";
  opacity: 0.3;
  cursor: pointer;
}
.resources-collection .collection-pic .bottom .bottom-title .bottom-left .ant-btn .e {
  position: absolute;
  right: 5px;
  bottom: 7px;
  width: 0;
  height: 0;
  border-right: 4px solid transparent;
  border-left: 4px solid transparent;
  border-top: 4px solid #fff;
  content: "";
  opacity: 0.3;
  cursor: pointer;
}
.resources-collection .collection-pic .bottom .bottom-title .bottom-left .ant-btn .i.active,
.resources-collection .collection-pic .bottom .bottom-title .bottom-left .ant-btn .e.active {
  opacity: 1;
}
.resources-collection .collection-pic .bottom .bottom-title .bottom-left .buta .ant-btn {
  background: #EFEFEF;
  border: 1px solid #EFEFEF;
  color: #000000;
  opacity: 0.6;
}
.resources-collection .collection-pic .bottom .bottom-title .bottom-left .buta .ant-btn .i {
  border-bottom: 4px solid #BEBEBE;
}
.resources-collection .collection-pic .bottom .bottom-title .bottom-left .buta .ant-btn .e {
  border-top: 4px solid #BEBEBE;
}
.resources-collection .collection-pic .bottom .bottom-title .bottom-left .buta.ant-btn-primary {
  width: 110px;
  height: 32px;
  background: #3BB9FF !important;
  border-radius: 2px;
}
.resources-collection .collection-pic .bottom .bottom-title .bottom-left .buta.ant-btn-primary span {
  color: #fff !important;
}
.resources-collection .collection-pic .bottom .bottom-title .bottom-right {
  float: right;
  display: inline-block;
}
.resources-collection .collection-pic .bottom .bottom-title .bottom-right .span {
  float: right;
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 30px;
  color: #000000;
  opacity: 0.3;
  margin: 0 20px;
}
.resources-collection .collection-pic .bottom .bottom-title .bottom-right .inp {
  width: 217px;
  height: 30px;
}
.resources-collection .collection-pic .bottom .bottom-title .bottom-right .inp .ant-input {
  background: #F4F4F4;
  border-radius: 13.5px;
  line-height: 30px;
  height: 30px;
  border: none;
  padding: 4px 11px 4px 30px;
  font-weight: normal;
  font-size: 12px;
  color: #000000;
  /*opacity: 0.3;*/
}
.resources-collection .collection-pic .bottom .bottom-title .bottom-right .inp .ant-input-suffix {
  left: 10px;
  right: auto;
  font-size: 16px;
  color: #CDCDCD;
}
.resources-collection .collection-pic .bottom .bottom-list {
  width: 100%;
  margin: 0 !important;
  padding-top: 40px;
}
.resources-collection .collection-pic .bottom .bottom-list .gutter-row {
  width: 14%;
  float: left;
  padding: 30px !important;
  position: relative;
  margin-bottom: 2%;
  background: #FAFAFA;
  border: 1px solid #EDEDED;
  box-sizing: border-box;
  border-radius: 3px;
  margin-right: 2%;
}
.resources-collection .collection-pic .bottom .bottom-list .gutter-row .img {
  width: 14px;
  float: left;
  position: absolute;
  left: 30px;
  top: 26px;
}
.resources-collection .collection-pic .bottom .bottom-list .gutter-row .img img {
  display: inline-block;
  width: 100%;
}
.resources-collection .collection-pic .bottom .bottom-list .gutter-row .text {
  padding: 0 0 0 30px;
  display: block;
}
.resources-collection .collection-pic .bottom .bottom-list .gutter-row .text .title {
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 120.7%;
  color: #000000;
  margin-bottom: 5px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  height: 20px;
  display: block;
}
.resources-collection .collection-pic .bottom .bottom-list .gutter-row .text .span {
  font-style: normal;
  font-weight: normal;
  font-size: 13px;
  line-height: 120.7%;
  color: #000000;
  opacity: 0.3;
  display: block;
  min-height: 16px;
}
.resources-collection .collection-pic .bottom .bottom-list .gutter-row .text .link {
  font-style: normal;
  font-weight: normal;
  font-size: 13px;
  line-height: 120.7%;
  text-decoration-line: underline;
  color: #000000 !important;
  display: block;
  margin-top: 10px;
}
.resources-collection .collection-pic .bottom .Space-bottom {
  float: right;
}
.resources-collection .paper-right-bottom {
  margin-top: 20px;
  overflow: hidden;
  /* box-shadow: 2px 8px 16px -8px rgba(29, 32, 31, 0.12);*/
  border-radius: 6px;
  background: #fff;
}
.resources-collection .paper-right-bottom .question-select {
  padding: 0 15px;
  text-align: center;
}
.resources-collection .paper-right-bottom .ant-card-head {
  border-bottom: none;
}
.resources-collection .rside-col {
  margin-bottom: 15px;
  width: 16.66%;
}
.resources-collection .rside-col dl {
  display: flex;
  background: #FAFAFA;
  border: 1px solid #EDEDED;
  box-sizing: border-box;
  border-radius: 6px;
  overflow: hidden;
  min-width: 212px;
}
.resources-collection .rside-col dt .img {
  width: 47px;
  height: 83px;
  position: relative;
}
.resources-collection .rside-col dt .img .img01 {
  width: 47px;
  height: 83px;
}
.resources-collection .rside-col dt .img .img02 {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  display: block;
  height: 15px;
  width: auto;
}
.resources-collection .rside-col dd {
  padding: 12px 10px 0;
  box-sizing: border-box;
  width: 80%;
}
.resources-collection .rside-col dd div {
  color: #000;
  padding-left: 5px;
  cursor: pointer;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  width: 100%;
}
.resources-collection .rside-col dd p {
  margin: 0;
}
.resources-collection .rside-col dd p span {
  margin: 0 5px;
  color: rgba(0, 0, 0, 0.3);
  font-size: 12px;
}
.resources-collection .rside-col .link {
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  text-decoration-line: underline;
  color: #000000 !important;
  display: inline-block;
  height: 22px;
  line-height: 22px;
  padding-left: 5px;
  cursor: pointer;
}
.resources-collection .rside-col dl:hover div {
  color: #3BB9FF;
}
.resources-collection .rside-cola {
  width: 16.66% !important;
}
.resources-collection .paper-common {
  padding: 0 10px 0 10px;
}
.resources-collection .paper-common .ant-col-23 {
  margin-left: 20px;
  width: calc(100% - 100px);
}
.resources-collection .paper-common .paper-year {
  color: #2A283D;
  font-weight: bold;
  display: inline-block;
  text-align: center;
  height: 30px;
  line-height: 30px;
}
.resources-collection .paper-common .paper-common-btn .ant-btn {
  border: none;
  background: none;
  margin: 0 10px 5px 0;
  background: #EFEFEF;
  border-radius: 2px;
}
.resources-collection .paper-common .paper-common-btn .ant-btn.ant-btn-primary {
  width: 110px;
  height: 32px;
  background: #3BB9FF !important;
  border-radius: 2px;
}
.resources-collection .paper-common .paper-common-btn .ant-btn.ant-btn-primary span {
  color: #fff !important;
}
.resources-collection .paper-common .paper-common-btn .ant-btn span {
  font-size: 14px;
  color: #000;
  font-weight: 400;
}
.resources-collection .paper-common .ant-radio-button-wrapper {
  color: #000;
  background: none;
  border: none;
}
.resources-collection .paper-common .ant-radio-button-wrapper span {
  color: #2A283D;
  opacity: 0.6;
}
.resources-collection .paper-common .ant-radio-button-wrapper::before {
  width: 0;
}
.resources-collection .paper-common .ant-radio-button-wrapper.ant-radio-button-wrapper-checked {
  background: #3BB9FF !important;
  border-radius: 2px;
  height: 26px;
  line-height: 26px;
  color: #fff;
}
.resources-collection .paper-common .ant-radio-button-wrapper.ant-radio-button-wrapper-checked span {
  color: #fff !important;
  opacity: 1;
}
.resources-collection .paper-common .actives {
  color: #2cbde6;
  border-color: #2cbde6;
}
.resources-collection .paper-common .ant-divider-horizontal {
  margin: 16px 0;
}
.resources-collection .paper-common .paper-common-search {
  text-align: right;
}
.resources-collection .paper-common .paper-common-search .ant-input-affix-wrapper .ant-input {
  background: #F4F4F4;
  border-radius: 13.5px;
  height: 32px;
  opacity: 0.7;
}
.resources-collection .paper-common .paper-common-search .paper-common-totale {
  margin-left: 20px;
  color: #000000;
  opacity: 0.3;
}
.resources-collection .paper-common-students .paper-common-btn .ant-btn {
  border: none;
  background: none;
  margin: 0 10px 5px 0;
  background: #EFEFEF;
  border-radius: 2px;
}
.resources-collection .paper-common-students .paper-common-btn .ant-btn.ant-btn-primary {
  width: 110px;
  height: 32px;
  background: #3BB9FF !important;
  border-radius: 2px;
}
.resources-collection .paper-common-students .paper-common-btn .ant-btn.ant-btn-primary span {
  color: #fff !important;
}
.resources-collection .paper-common-students .paper-common-btn .ant-btn span {
  font-size: 14px;
  color: #000;
  font-weight: 400;
}
.resources-collection .ant-divider {
  padding: 0 10px;
  margin: 24px 10px;
}
.resources-collection .ant-divider-horizontal {
  width: auto;
  min-width: auto;
}
.resources-collection .center-content-search {
  margin-bottom: 30px;
}
.resources-collection .center-content-search .center-content-paper .ant-card-body {
  padding: 30px 20px 0 20px;
}
.resources-collection .center-content-search .center-content-paper .ant-row {
  margin-bottom: 30px;
}
html {
  overflow-x: auto;
  overflow-y: auto;
}
